.emptyCart {
    margin: auto;
    text-align: center;
    padding: 10vmax;
    height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
 
  }
  .emptyCart > svg {
    font-size: 5vmax;
    color: #3BB77E;
  }
  .emptyCart > p {
    font-size: 2vmax;
  }
  .emptyCart > a {
    background-color: #3BB77E;
    color: rgb(255, 255, 255);
    border: none;
    padding: 1vmax 3vmax;
    cursor: pointer;
    font: 400 1vmax "Roboto";
    margin-top: 1%;
    text-decoration: none;
 
  }
  
  .cartPage {
    padding: 5vmax;
  }
  
  .cartHeader {
    background-color: #3BB77E;
    width: 90%;
    box-sizing: border-box;
    margin: auto;
    color: rgb(36, 36, 36);
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;
    font: 300 0.7vmax "Roboto";
    font-size: 1.2rem;
  }
  .cartHeader > p {
  
    margin: 10px;
  }
  .cartHeader > p:last-child {
    text-align: end;
  }

  .cartContainer {

    width: 90%;
    margin: auto;
    border-bottom: 1px solid #99999942;
    display: grid;
    grid-template-columns: 4fr 1fr 1fr;
    
  }
  .cartContainer a{
    color: black;
  }

  .cartInput {
    display: flex;
    align-items: center;
    height: 8vmax;
  }
  
  .cartInput > button {
    border: none;
    background-color: #3BB77E;
    padding: 0.5vmax;
    cursor: pointer;
    color: rgb(255, 255, 255);
    transition: all 0.5s;
  }
  .cartInput > button:hover {
    background-color:#3BB77E;
  }
  
  .cartInput > input {
    border: none;
    padding: 0.5vmax;
    width: 3vmax;
    text-align: center;
    outline: none;
    font: 400 1vmax "Roboto";
    color: rgba(0, 0, 0, 0.74);
  }
  
  .cartSubtotal {
    display: flex;
    padding: 0.5vmax;
    height: 8vmax;
    align-items: center;
    box-sizing: border-box;
    font: 300 1.3vmax cursive;
    justify-content: flex-end;
    color: rgba(0, 0, 0, 0.753);
  }
  
  .cartGrossProfit {
    display: grid;
    grid-template-columns: 2fr 1.2fr;
  }
  
  .cartGrossProfitBox {
    border-top: 3px solid #3BB77E;
    margin: 1vmax 4vmax;
    box-sizing: border-box;
    padding: 2vmax 0;
    font: 300 1.3vmax "Roboto";
    display: flex;
    justify-content: space-between;
    color: rgb(16, 16, 16);
  }
  
  .checkOutBtn {
    display: flex;
    justify-content: flex-end;
  }
  .checkOutBtn > button {
    background-color: #3BB77E;
    color: white;
    border: none;
    padding: 0.8vmax 3vmax;
    font: 300 1.3vmax "Roboto";
    margin: 1vmax 4vmax;
    cursor: pointer;
  }
  
  @media screen and (max-width: 600px) {
    .cartPage {
      padding: 0;
    }
    
  
    .cartHeader {
      width: 100%;
      font: 300 1.7vmax "Roboto";
      grid-template-columns: 3fr 1fr 1fr;
    }
  
    .cartContainer {
      width: 100%;
      grid-template-columns: 3fr 1fr 1fr;
    }
  
    .cartInput {
      height: 20vmax;
     
    }
  
    .cartInput > button {
      padding: 1.5vmax;
    }
  
    .cartInput > input {
      width: 5vmax;
      padding: 1.5vmax;
      font: 400 1.8vmax "Roboto";
    }
  
    .cartSubtotal {
      padding: 1.5vmax;
      height: 20vmax;
      font: 300 2vmax "Roboto";
    }
  
    .cartGrossProfit {
      display: grid;
      grid-template-columns: 0fr 2fr;
    }
  
    .cartGrossProfitBox {
      padding: 2vmax;
      font: 300 2vmax "Roboto";
    }
  
    .checkOutBtn > button {
      padding: 2vmax 4vmax;
      width: 100%;
      font: 300 2vmax "Roboto";
    }
  }

  @media screen and (max-width: 420px) {
    .checkOutBtn > button {
      padding: .5vmax 1vmax;
      width: 100%;
      font: 300 2vmax "Roboto";
    }
      
    .cartInput {
      height: 5vmax !important;
      padding: 0;
      margin: auto;
      text-align: center;
      height: 20px;
     
    }
  
    .cartInput > button {
     
      padding: 1vmax;
    }
  
    .cartInput > input {
      width: 2vmax;
      padding: .1vmax;
      font: 400 1.8vmax "Roboto";
    }
    .cartSubtotal {
     align-self: center;
      height: 5vmax;
      font: 300 2vmax "Roboto";
    }

  }